<script>
import { LABELS } from '../utils/constant.js';
import ProductItem from './ProductItem.svelte';

export let json;
export let resultLoading;
export let type;

function getCategoryName(code) {
  return LABELS[LABELS[code]]['en-US'];
}
</script>

<div id="result-list" class:loading={resultLoading}>
  {#if json === null}
    <!-- Lading state or something went worng. -->
  {:else}
    {#if type === 'recommend' && Object.keys(json).length > 0}
      {#each Object.keys(json) as categoryId}
        <h2>{getCategoryName(categoryId)}</h2>
        <div class="grid-list">
          {#each json[categoryId] as product}
            <ProductItem product={product} />
          {/each}
        </div>
      {/each}
    {:else if type === 'recommend-multi' && json.length > 0}
      {#each json as set, i}
        <h2>set {i + 1}</h2>
        <div class="grid-list">
          {#each set as product}
            <ProductItem product={product} />
          {/each}
        </div>
      {/each}
    {:else if type === 'search' && json.length > 0}
      <div class="grid-list">
        {#each json as product}
          <ProductItem product={product} />
        {/each}
      </div>
    {:else}
      No results.
    {/if}
  {/if}
</div>
