<script>
import { GENDER_MASK, M_BIT, F_BIT, RELATIONS, LABELS, CATEGORY, CATEGORIES_FOR_RECOMMEND } from '../utils/constant.js';

export let type;
export let region;
export let genderOption = null;
export let categoryOption = [];
export let subCategoryOption = undefined;
export let flexmodeOption;
export let onFilterChange;

const storedFilterOpen = window.localStorage.getItem('filteropen');
let isFilterOpen = storedFilterOpen === 'false' ? false : true;
let categories = false;
let subCategories = false;
$: {
  if (region === undefined) {
    categories = false;
  } else if (region.category && type === 'search') { // v1 search
    categories = Object.keys(CATEGORY).filter(id => {
      return CATEGORY[id].type === CATEGORY[region.category.code.toString()].type;
    }).map(code => ({
      code: parseInt(code),
      name: CATEGORY[code].str,
    }));
  } else if (region.category) { // v1 stylist
    categories = Object.keys(CATEGORY).filter(id => {
      return CATEGORIES_FOR_RECOMMEND.includes(region.category.code) && CATEGORIES_FOR_RECOMMEND.includes(parseInt(id));
    }).map(code => ({
      code: parseInt(code),
      name: CATEGORY[code].str,
    }));
  } else if (type === 'search') {  // v2 search
    const c1 = LABELS[region.category1.code.toString()];
    categories = RELATIONS['c1_relation'][c1].child.map(c2 => ({
      code: LABELS[c2]['code'],
      name: LABELS[c2]['en-US'],
    }));
  } else {  // TODO: v2 stylist
    categories = false;
  }
}
$: {
  if (region === undefined) {
    subCategories = false;
  } else if (region.category && type === 'search') { // v1 search
    subCategories = CATEGORY[categoryOption]['sub_categories']
      .map(s => ({
        code: s.id,
        name: s.str,
      }));
  } else if (region.category) { // v1 stylist
    subCategories = false;
  } else if (type === 'search') {  // v2 search
    subCategories = RELATIONS['c2_relation'][LABELS[categoryOption]].child
      .map(c3 => ({
        code: LABELS[c3]['code'],
        name: LABELS[c3]['en-US'],
      }));
  } else {  // v2 stylist
    subCategories = false;
  }
  if (subCategories !== false && subCategories.map(c => c.code).indexOf(subCategoryOption) === -1) {
    subCategoryOption = undefined;
  }
}

function toggleFilter() {
  isFilterOpen = !isFilterOpen;
  window.localStorage.setItem('filteropen', isFilterOpen);
}

function get_gender(gender_code) {
  if (typeof(gender_code) === "string") {
    if (gender_code === "female" || gender_code === "Female")
      return F_BIT;
    if (gender_code === "male" || gender_code === "Male")
      return M_BIT;
    if (gender_code === "unisex" || gender_code === "Unisex")
      return F_BIT | M_BIT;
    return 0;
  }
  if (typeof(gender_code) === "number") {
    if ((gender_code&GENDER_MASK) === F_BIT)
      return "Female";
    if ((gender_code&GENDER_MASK) === M_BIT)
      return "Male";
    return "Unisex";
  }
}
</script>

<div id="search-options" class:on={isFilterOpen}>
  <button class="toggle" on:click={toggleFilter}>Filter</button>
  <div class="options">
    <div id="search-option-gender">
      <div class="label small">GENDER</div>
      <div class='gender-list'>
        {#each [M_BIT, F_BIT, GENDER_MASK] as gender}
        <label>
          {get_gender(gender)}
          <input type="radio" value={gender} bind:group={genderOption} on:change={onFilterChange}>
        </label>
        {/each}
      </div>
    </div>
    {#if categories && categories.length > 0}
    <div id="search-option-category">
      <div class="label small">CATEGORY</div>
      <div class='category-list'>
        {#each categories as category (category.code)}
        <label>
          {category.name}
          <input type="radio" value={category.code} bind:group={categoryOption} on:change={onFilterChange}>
        </label>
        {/each}
      </div>
    </div>
    {/if}
    {#if subCategories && subCategories.length > 0}
    <div id="search-option-subcategory" class="on">
      <div class="label small">SUB CATEGORY</div>
      <div class='category-list'>
        <label>
          None
          <input type="radio" value={undefined} bind:group={subCategoryOption} on:change={onFilterChange}>
        </label>
        {#each subCategories as subCategory}
        <label>
          {subCategory.name}
          <input type="radio" value={subCategory.code} bind:group={subCategoryOption} on:change={onFilterChange}>
        </label>
        {/each}
      </div>
    </div>
    {/if}
    <!-- <div id="searchable_attribute_list"></div> -->
    {#if type === 'search'}
    <div id="search_mode">
      <div class="label small">MODE</div>
      <div class="mode">
        <label>
          Filter
          <input type="radio" value="filter" bind:group={flexmodeOption} on:change={onFilterChange}>
        </label>
        <label>
          Naive
          <input type="radio" value="naive" bind:group={flexmodeOption} on:change={onFilterChange}>
        </label>
      </div>
    </div>
    {/if}
  </div>
</div>
