class PXL {
  constructor(apiBase, apikey, errorHandler) {
    this.set(apiBase, apikey);
    this.errorHandler = errorHandler;
  }

  set(apiBase, apikey) {
    this.apiBase = apiBase;
    this.apikey = apikey;
  }

  detect(url) {
    return this.request(`/detect?url=${url}`);
  }

  detect_by_file(element) {
    const formData = new FormData();
    formData.append('file', element.files[0]);
    return this.request(`/detect`, {
      method: 'POST',
      body: formData,
    }, true);
  }

  analyze(url) {
    return this.request(`/analyze?url=${url}&lang=en`);
  }

  search(region, gender, c2, c3, attrs, flex_mode, count) {
    let url = `/search/${region.id}`;
    if (this.apiBase.indexOf('/v1') > -1) {
      let searchCategory = gender | c2;
      const flex_query = {};
      if (c3 !== undefined) {
        flex_query["sub_category"] = c3;
      }
      // FIXME: Support attribute search
      if (attrs && attrs.length > 0) {
        flex_query["attributes"] = attrs;
      }
      if (typeof region.id === 'string' && region.id.indexOf('_') < 0 && region.id.indexOf(',') < 0) {  // tonic
        return this.request(`${url}?count=${count}`);
      }
      return this.request(`${url}?gender=${gender}&category=${searchCategory}&count=${count}&mode=${flex_mode}`, {
        method: 'POST',
        body: JSON.stringify(flex_query),
      });
    }
    return this.request(`${url}?genders=${gender}&category=${c3 ? c3 : c2}&count=${count}&mode=${flex_mode}`);
  }

  recommend(region, mode, search_category, count) {
    const path = this.apiBase.indexOf('/v1') > -1 ? `/recommend/stylist/${region.id}` : `/stylist/${region.id}`;
    const query = region.category ? `category=${search_category}` : `genders=${region.gender.code}&category=${region.category2 ? region.category2.code : region.category1.code}`
    return this.request(`${path}?mode=${mode}&count=${count}&${query}`);
  }

  request(query, options, isFile) {
    const is_apikey = this.apikey.length > 56;
    const headers = {
      [is_apikey ? "ApiKey" : "bucketname"]: this.apikey,
    };
    if (isFile !== true) {
      headers["Content-Type"] = "application/json";
    }
    return fetch(`${this.apiBase}${query}`, {
      ...options,
      headers,
    }).then(res => {
      if (res.ok) {
        return res.json();
      }
      this.errorHandler && this.errorHandler(res.message);
    }).catch(err => {
      this.errorHandler && this.errorHandler(`${err.name}: ${err.message}`);
      return null;
    });
  }
}

export default PXL;
